import { Types, model, Document, Model, Schema } from 'mongoose';
import { composeMongoose } from 'graphql-compose-mongoose';

export type installmentsCadenceEnum =
  | 'daily'
  | 'weekly'
  | 'biweekly'
  | 'monthly';

export interface ICommerceRiskParam {
  _id?: any;
  commerce: string;
  withCreditScoring: boolean;
  creditScoreLowerLimit: number;
  creditScoreUpperLimit: number;
  name: string;
  installmentsCadence: installmentsCadenceEnum;
  installments: number;
  minAmountUSD: number;
  maxAmountUSD: number;
  interestRate: number;
  active?: boolean;
  createdAt?: Date;
  updatedAt?: Date;
}

export type CommerceRiskParamDocument = Document<
  Types.ObjectId,
  any,
  ICommerceRiskParam
> &
  ICommerceRiskParam;

const commerceRiskParamSchema = new Schema<ICommerceRiskParam>(
  {
    commerce: {
      type: String,
      trim: true,
    },
    withCreditScoring: {
      type: Boolean,
    },
    creditScoreLowerLimit: {
      type: Number,
    },
    creditScoreUpperLimit: {
      type: Number,
    },
    name: {
      type: String,
    },
    installmentsCadence: {
      type: String,
      enum: ['daily', 'weekly', 'biweekly', 'monthly'],
    },
    installments: {
      type: Number,
    },
    minAmountUSD: {
      type: Number,
    },
    maxAmountUSD: {
      type: Number,
    },
    interestRate: {
      type: Number,
    },
    active: {
      type: Boolean,
      default: true,
    },
  },
  { timestamps: true }
);

export const CommerceRiskParam = model<
  ICommerceRiskParam,
  Model<ICommerceRiskParam>
>('CommerceRiskParam', commerceRiskParamSchema);

export const CommerceRiskParamTC = composeMongoose<CommerceRiskParamDocument>(
  CommerceRiskParam as any
);
